// Contact Section Styling

#contact {
  background: $gradient-firewatch;
  color: $secondary-text-color;
  padding: 0; }

.contact-form-wrapper {
  width: 90%;
  max-width: 90%;
  padding: 2rem 0;
  margin: 0 auto; }

.contact-form-info-group {
  width: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  label {
    margin-bottom: 1rem; }
  input {
    width: 100%;
    margin-top: .5rem;
    padding: .75rem;
    display: flex;
    flex-direction: column; } }

.contact-form-message {
  width: 100%;
  max-width: 100%;
  margin-bottom: 1.5rem;
  textarea {
    margin-top: .5rem;
    padding: .75rem;
    width: 100%; } }

.contact-form-submit {
  .button {
    font-size: 1.25rem;
    border: 3px solid $secondary-text-color;
    color: $secondary-text-color;
    transition: $primary-transition;
    &:hover {
      border: 3px solid $highlight-color-alt;
      color: $highlight-color-alt; } } }


/* Media Queries */

@media screen and (min-width: $tablet-breakpoint) {
  .contact-form-wrapper {
    width: 80%;
    max-width: 80%; }

  .contact-form-info-group {
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between; }

  .contact-form-message {
    width: 100%;
    max-width: 100%; } }

@media screen and (min-width: $desktop-breakpoint) {
  .contact-form-wrapper {
    width: 60%;
    max-width: 60%; }

  .contact-form-info-group {
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between; }

  .contact-form-message {
    width: 100%;
    max-width: 100%; } }
