// Typography Styling

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-family: $font-family-primary;
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, h5, h6,
.huge, .x-large, .large, .medium, .small, .x-small {
  margin: 0 0 1rem 0;
  padding: 0; }

h1, h2, h3, h4, h5, h6 {
  font-family: $font-family-secondary; }

.huge, h1 {
  font-size: 2rem;
  line-height: 1.2; }

.x-large, h2 {
  font-size: 1.5rem;
  line-height: 1.2; }

.large, h3 {
  font-size: 1.375rem;
  line-height: 1.2; }

.medium, h4 {
  font-size: 1.125rem;
  line-height: 1.2; }

.small, h5 {
  font-size: 1rem;
  line-height: 1.2; }

.x-small, h6 {
  font-size: 0.875rem;
  line-height: 1.2; }

p {
  font-size: 1.125rem;
  line-height: 1.2;
  padding: 10px 0; }

em, .text-italic {
  font-style: italic; }

.text-center {
  text-align: center; }

a {
  color: $highlight-color; }
